.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(135deg, #cce7ff, #e0c3fc);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App {
  max-width: 600px;
  margin: auto;
  text-align: center;
  padding: 1rem;
}

h2 {
  margin-bottom: 1rem;
  color: #222;
}

nav {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 0.5rem 1rem;
  margin-bottom: 1rem;
  border-radius: 6px;
}

nav a {
  color: #222;
  font-weight: 600;
  text-decoration: none;
  margin: 0 8px;
  transition: color 0.2s;
}

nav a:hover {
  color: #555;
}

button {
  background: #222;
  color: #fff;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 1rem;
}

button:hover {
  background: #555;
}

div {
  color: #222;
}

/* Wheel container styling */
.wheel-container {
  background: rgba(255, 255, 255, 0.3);
  padding: 1rem;
  border-radius: 8px;
}